export default {
	data() {
		return {
			losType: '',
			lostName: '',
			typeRange: ['全部','考研教材','电子产品','校园卡','其他'],
			typeRangeNum: 0,
			owerName: '',
			owerTel: '',
			owenAddress: '',
			imageSrc:'',
			isActive: true
			
		}
	},
	methods: {
		typeSelect(e){
			this.typeRangeNum = e.detail.value;
		},
		takePhoto(){
			let that = this;
			uni.showModal({
				title:'图片选择',
				content:'请选择获取图片的方式',
				cancelText:'拍照',
				confirmText:'相册',
				success(e) {
					console.log(e)
					if(e.cancel){
						let ctx = uni.createCameraContext();
						that.isActive = false;
						ctx.takePhoto({
							quality:'high',
							success:function(e){
								console.log(e);
								this.imageSrc = e.tempImagePath;
							},
							fail(err) {
								console.log(err);
							}
						})
					}else{
						uni.chooseImage({
							success(e) {
								this.imageSrc = e.tempFiles[0];
							},
							fail(err){
								console.log(err);
							}
						})
					}
				}
			})
		},
		
	
	}
}
